* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

#html {
  word-break: break-all;
}

#pig {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  top: 20px;
}

@media (max-width: 500px) {
  #html {
    height: 60vh;
    overflow: auto;
  }

  #div1 {
    height: 40vh;
  }

  #pig {
    position: relative;
    top: 20%;
    right: 20%;
  }
}
